<template>
  <v-h1>Legal claim submission form</v-h1>

  <p>We take copyright infringement seriously. We ask you to help us to ensure we address it promptly and effectively.</p>

  <p>Please make sure that your claim includes:<br /><br /></p>

  <ul>
    <li>A permission for appdb to send you link to verify your identity (once) in order to complete claim submission process;</li>
    <li>Information reasonably sufficient to permit appdb to contact you: name, email, phone number, company information;</li>
    <li>
      A statement that you have a good faith belief that use of the material in the manner complained of is not authorized by the copyright owner, its
      agent, or the law; and
    </li>
    <li>
      A statement that, under penalty of perjury, the information in the claim is accurate and you are authorized to act on behalf of the owner of the
      exclusive right that is allegedly infringed
    </li>
  </ul>

  <p>
    <br /><br />
    In order to prevent infringements in the future, we are welcoming you to create
    <a
      :href="config.public.registerURL"
      target="_blank"
    >
      an appdb Publisher
    </a>
    account to publish and monetise your app on appdb and use our content tagging system that automatically prevents users from uploading of
    unlicensed copies of your app.<br /><br />
  </p>

  <p class="my-7">Please fill this form in order to submit a claim:</p>

  <form class="grid grid-cols-4 gap-5">
    <v-label label="Full name">
      <v-input v-model="form.name" />
    </v-label>

    <v-label label="Corporate email">
      <v-input
        v-model="form.email"
        type="email"
      />
    </v-label>

    <v-label label="Phone number in international format">
      <v-input
        v-model="form.phone"
        type="tel"
      />
    </v-label>

    <v-label label="URL where infringement content is located">
      <v-input
        v-model="form.url"
        type="url"
      />
    </v-label>

    <v-label
      label="Claim text"
      class="col-span-full"
    >
      <v-textarea
        v-model="form.license_text"
        :rows="3"
      />
    </v-label>

    <div class="col-span-full">
      <v-checkbox v-model="areTermsAccepted">
        By checking this box I agree with

        <span
          v-for="agreement of agreements"
          :key="agreement.id"
          class="not-last:after:content-[',_']"
        >
          <a
            :href="agreement.uri"
            target="_blank"
          >
            {{ agreement.name }}
          </a>
        </span>
      </v-checkbox>
    </div>

    <div class="col-span-full">
      <v-button
        :loading="isSubmitting"
        variant="primary"
        @click="submit"
      >
        Submit
      </v-button>
    </div>
  </form>
</template>

<script setup lang="ts">
import { useRecaptcha } from "core/composables/useRecaptcha"
import { clone } from "lodash-es"

useHead({
  title: "Copyright Claim",
})

definePageMeta({
  requiredRole: "any",
  layout: "no-menu",
})

const config = useRuntimeConfig()

const isSubmitting = ref(false)
const emptyForm: Parameters<typeof submitCopyrightClaim>[0] = {
  url: "",
  name: "",
  phone: "",
  email: "",
  license_text: "",
  "g-recaptcha-response": "",
}
const form = ref(clone(emptyForm))
const areTermsAccepted = ref(false)

const generateRecaptchaToken = useRecaptcha()
async function setRecaptchaToken() {
  const user = useUserStore()
  if (user.isAuthorized) {
    form.value["g-recaptcha-response"] = ""
    return
  }

  form.value["g-recaptcha-response"] = await generateRecaptchaToken()
}

async function submit() {
  const { notifySuccess, notifyError } = useNotification()

  if (!areTermsAccepted.value) {
    notifyError("You have to agree with terms and conditions", { id: "submit_copyright_claim" })
    return
  }

  isSubmitting.value = true
  try {
    await setRecaptchaToken()
    await submitCopyrightClaim(form.value)
    notifySuccess("Your claim has been submitted. Please check your mailbox for further instructions", { id: "submit_copyright_claim" })
    form.value = clone(emptyForm)
  } finally {
    isSubmitting.value = false
  }
}

const agreements = await getAgreements({ required_for: "copyright_claims" })
</script>
